import React, { Component } from 'react';
import styles from './menu.module.scss';
import styled from 'styled-components';
import BgVideo from '../../components/BgVideo';
import { Link } from 'react-router-dom';


const Dot=styled.div`
    height: min(50px,12vw);
    width: min(50px,12vw);
    border-radius: 50%;
    display: flex;
    position:fixed;
    right: min(50px,10%);
    bottom: 0;
    margin-bottom: 60px;
    // pointer-events: null;
    cursor: pointer;
    z-index: 10000;
    overflow: hidden;
    background-color: rgba(255,255,255,0.6);
    // border-style: solid;
    // border-width: 1px;
    // border-color: rgba(255,255,255,0.9);
    // opacity:15%;
    color: rgba(0,180,0,1);
    justify-content:center;
    align-items:center;
    font-weight: 900;
    font-size: 16px;
    // filter: blur(5px);
    filter: drop-shadow(0 0 15px white);
`
let windowX = document.documentElement.clientWidth,
windowY = document.documentElement.clientHeight;

export default class index extends Component {
    constructor(props){
        super(props)
        this.state={
            tmp: 1,
        }
    }
    handleMouseEnter(target){
        this.setState({tmp:target})
    }
    handleMouseLeave(){
        this.setState({tmp: 0})
    }
    handleClick(){
        window.history.back();
    }
    render() {
        return <div className={ styles.wrap } style={{background:'rgba(0,255,0,0.8)',width:'100%',height:'100%'}}>
            <BgVideo/>
            <Dot
                style={{
                    // color: windowX>windowY?'':'white',
                    // right: windowX>windowY?'':'10%',
                    fontSize: windowX>windowY?'':'14px',
                    // backgroundColor: windowX>windowY?'':'rgba(255,255,255,0.6)',
                    height: this.state.tmp===1?'min(75px,18vw)':'min(75px,18vw)',
                    width: this.state.tmp===1?'min(75px,18vw)':'min(75px,18vw)',
                    right: this.state.tmp===1?'37.5px':'37.5px',
                    marginBottom: this.state.tmp===1?'47.5px':'47.5px',
                    // filter: windowX>windowY?'':'drop-shadow(0 0 15px white)'
                    }}
                onClick={this.handleClick.bind(this)}

                // onMouseEnter={this.handleMouseEnter.bind(this, 1)}
                // onMouseLeave={this.handleMouseLeave.bind(this)}
                >
                MENU</Dot>

            <section className={ styles.brief }>
                {windowX>windowY?null:<br />}
                {/* <br />
                <h3 style={{
                    }}>8 Years of Multi-disciplinary Design Training</h3>
                <h3 style={{
                    }}>5 Years of Robotic Fabrication Experience</h3>
                <h3 style={{
                    marginBottom:'10px'}}>4 Years of Human-AI Interaction Experience</h3> */}
                <Link to='/'>
                <h2 style={{
                    color:this.state.tmp==='Homepage'?'rgba(0,100,0,1)':'',
                    fontWeight:this.state.tmp==='Homepage'?'900':'',
                    }}
                onMouseEnter={this.handleMouseEnter.bind(this, 'Homepage')}
                onMouseLeave={this.handleMouseLeave.bind(this)}>Homepage</h2>
                </Link>
                <Link to='/works'>
                <h2 style={{
                    color:this.state.tmp==='Workspace'?'rgba(0,100,0,1)':'',
                    fontWeight:this.state.tmp==='Workspace'?'900':'',}}
                onMouseEnter={this.handleMouseEnter.bind(this, 'Workspace')}
                onMouseLeave={this.handleMouseLeave.bind(this)}>Workspace</h2>
                </Link>
                <Link to='/contact'>
                <h2 style={{
                    color:this.state.tmp==='About'?'rgba(0,100,0,1)':'',
                    fontWeight:this.state.tmp==='About'?'900':'',
                }}
                onMouseEnter={this.handleMouseEnter.bind(this, 'About')}
                onMouseLeave={this.handleMouseLeave.bind(this)}>About Me</h2>
                </Link>


                {/* <br /><br /><br /> */}
                <h2 style={{
                    color:this.state.tmp==='Gallery'?'rgba(0,100,0,1)':'',
                    fontWeight:this.state.tmp==='Gallery'?'900':'',
                
                    marginTop:'70px',fontSize:'20px'}}
                onMouseEnter={this.handleMouseEnter.bind(this, 'Gallery')}
                onMouseLeave={this.handleMouseLeave.bind(this)}>Gallery</h2>

                <Link to='/Portfolio'><h2 style={{
                    color:this.state.tmp==='Edition'?'rgba(0,100,0,1)':'',
                    fontWeight:this.state.tmp==='Edition'?'900':'',
                
                    marginTop:'10px',fontSize:'20px'}}
                onMouseEnter={this.handleMouseEnter.bind(this, 'Edition')}
                onMouseLeave={this.handleMouseLeave.bind(this)}>Special Edition</h2></Link>

                <h2 style={{
                    color:this.state.tmp==='Publication'?'rgba(0,100,0,1)':'',
                    fontWeight:this.state.tmp==='Publication'?'900':'',
                
                    marginTop:'10px',fontSize:'20px'}}
                onMouseEnter={this.handleMouseEnter.bind(this, 'Publication')}
                onMouseLeave={this.handleMouseLeave.bind(this)}>Publication</h2>

                <h2 style={{
                    color:this.state.tmp==='Implementation'?'rgba(0,100,0,1)':'',
                    fontWeight:this.state.tmp==='Implementation'?'900':'',
                
                    marginTop:'10px',fontSize:'20px'}}
                onMouseEnter={this.handleMouseEnter.bind(this, 'Implementation')}
                onMouseLeave={this.handleMouseLeave.bind(this)}>Re-Implementation</h2>

                
                
            </section>
            


        </div>
    }
}