import React, { Component } from 'react';
import styled from 'styled-components';


const Collectionvideo=styled.div`
    position:fixed;
    right:0;
    bottom:0;
    object-fit:fill;
    min-height: 100%;
    min-width: 100%;
    // transform:translateX(-50%) translateY(-50%);
    pointer-events: none;
    // z-index: -10000;
    overflow: hidden;
    background-color: blue;
    opacity:15%;
`

export default class BgVideo extends Component {
    
    constructor(props){
        super(props)
        this.state={
            currentVideo: "jieyuantang_720p",
            videoList:["jieyuantang_720p","drone_720p","clay_extruder_720p","concrete_wall_720p","hybird_720p","luanan_720p","sincos_720p"],
        }
    }
    componentDidMount(){
        var arr=this.state.videoList
        var n=Math.floor(Math.random()*arr.length+1)-1
        this.setState({currentVideo: arr[n]})
    }
    render(){
        return(
        <Collectionvideo>
            <video
                style={{
                    position:'fixed',
                    right:'0',
                    bottom:'0',
                    minWidth:'100%',
                    minHeight:'100%',
                    width:'auto',
                    height:'auto',
                    backgroung:'url('+require("../../assets/images/000.png")+') no-repeat',
                    backgroundSize:'cover',
                    pointerEvents: 'none'
                }}
                autoPlay="autoplay"
                muted
                poster={require("../../assets/images/000.png")}
                src={require("../../assets/videos/"+ this.state.currentVideo +".mp4")}
                playsInline
                x5-video-player-type='h5'>
            </video>
        </Collectionvideo>
        )
        
    }
}
