import React, { Component } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';


const Dot=styled.div`
    height: min(50px,12vw);
    width: min(50px,12vw);
    border-radius: 50%;
    display: flex;
    position:fixed;
    right: min(50px,5%);
    bottom: 0;
    margin-bottom: 60px;
    user-select: none;
    // pointer-events: none;
    z-index: 10000;
    overflow: hidden;
    background-color: rgba(0,255,0,0.6);
    // border-style: solid;
    // border-width: 1px;
    // border-color: rgba(255,255,255,0.9);
    // opacity:15%;
    color: white;
    justify-content:center;
    align-items:center;
    font-weight: 500;
    font-size: 14px;
    // filter: blur(5px);
    // filter: drop-shadow(0 0 15px white);
`

const GreenMenu=styled.div`
    position:fixed;
    display:flex;
    background:rgba(0,255,0,1);
    width:100%;
    height:100%; 
    top:0;
    bottom:0;
    left:0;
    right:0;
    zIndex:9999;
`

let windowX = document.documentElement.clientWidth,
windowY = document.documentElement.clientHeight;

export default class Menu extends Component {
    constructor(props){
        super(props)
        this.state={
            tmp: 0,
        }
    }
    handleMouseEnter(target){
        this.setState({tmp:target})
    }
    handleMouseLeave(){
        this.setState({tmp: 0})
    }

    render(){
        return(
            <div>
            <Link to='/Menu'>
                {/* <GreenMenu/> */}
                <Dot
                style={{
                    color: windowX>windowY?'':'white',
                    // right: windowX>windowY?'':'10%',
                    fontSize: windowX>windowY?'':'12px',
                    // backgroundColor: windowX>windowY?'':'rgba(0,0,0,0.6)',
                    height: this.state.tmp?'min(75px,18vw)':'',
                    width: this.state.tmp?'min(75px,18vw)':'',
                    right: this.state.tmp?'37.5px':'',
                    marginBottom: this.state.tmp?'47.5px':'',
                    // filter: windowX>windowY?'':'drop-shadow(0 0 15px white)'
                    }}
                onMouseEnter={this.handleMouseEnter.bind(this, 1)}
                onMouseLeave={this.handleMouseLeave.bind(this)}>
                MENU</Dot>
                

                </Link>
            </div>
        )}
}