import React, { Component } from 'react';
import styles from './App.module.scss';
import { Link,Route,withRouter } from 'react-router-dom';
import HomePage from './HomePage'
import WorkPage from './WorkPage'
import ContactPage from './ContactPage'
import MenuPage from './MenuPage'

// import WorkPageDetail from './WorkPageDetail'
import Rockery from './WorkPageDetail/Rockery'
import Jieyuan from './WorkPageDetail/Jieyuan'
import Luanan from './WorkPageDetail/Emerging'
import TDBE from './WorkPageDetail/TDBE'
import AntiAR from './WorkPageDetail/AntiAR'
import FCollection from './WorkPageDetail/furnitureCollection'
import MxM from './WorkPageDetail/MxM'
import GreenA from './WorkPageDetail/GreenAnalyser'
import Mat from './WorkPageDetail/Mat'
import UrbanGAN from './WorkPageDetail/UrbanGAN'
import aiFAB from './WorkPageDetail/aiFAB'
import hybrid from './WorkPageDetail/hybridFormwork'
import geohash from './WorkPageDetail/geohash'
import snowFAB from './WorkPageDetail/snowFAB'
import Drone from './WorkPageDetail/Drone'
import Topology from './WorkPageDetail/Topology'
import DancePage from './DancePage'
import PortfolioPage from './PortfolioPage'



import { Scrollbars } from 'react-custom-scrollbars';
// import Nprogress from "nprogress";
// import "nprogress/nprogress.css";

class App extends Component {

    constructor(props) {
      super(props);
      this.scrollbarRef = React.createRef();
      this.state = {
        prevPosition: 0,
        isScrollUp: true,
        isPageBottom: false,
      };
    }

    handleScroll = (event) => {
      const currentPosition = event.target.scrollTop;
      this.setState({ 
        prevPosition: currentPosition,
        isScrollUp: (currentPosition == 0) || (this.state.prevPosition > currentPosition),
        // isPageBottom: currentPosition>=this.scrollbarRef.current.getScrollHeight()
      });
      // console.log(this.props.location.pathname)
      // console.log(currentPosition,this.state.prevPosition, this.state.isScrollUp, this.scrollbarRef.current.getScrollHeight(), this.state.isPageBottom)
    };

  render() {
    // console.log(location)
    return (
      <Scrollbars style={{ width: '100%', height: '100%' }}  onScroll={this.props.location.pathname=='/'?this.handleScroll:null}>
      <div className={styles.App}>

         {/* 内容区 */}
         <section className={styles.content}>
           <Route path="/" exact render={(props) => <HomePage {...props} isScrollUp={this.state.isScrollUp} />} />

           {/* <Route path="/" exact component={ HomePage } /> */}
           <Route path="/works" exact component={ WorkPage } />
           <Route path="/contact" exact component={ ContactPage } />
           <Route path="/doubleD" exact component={ DancePage } />
           <Route path="/portfolio" exact component={ PortfolioPage } />
           <Route path="/Menu" exact component={ MenuPage } />

           <Route path="/works/Jieyuan Shrine" exact component={ Jieyuan }/>
           <Route path="/works/Green Analyser" exact component={ GreenA }/>
           <Route path="/works/Travel Demand and Built Environment" exact component={ TDBE }/>
           <Route path="/works/In-between Mat" exact component={ Mat }/>
           <Route path="/works/Urban Mapping GAN" exact component={ UrbanGAN }/>
           {/* <Route path="/works/Urban Futures GAN" exact component={ Rockery }/>
           <Route path="/works/Coast GAN" exact component={ Rockery }/> */}
           <Route path="/works/Didi Geohash" exact component={ geohash }/>
           <Route path="/works/Hybrid Formwork" exact component={ hybrid }/>
           <Route path="/works/Subtractive Snow-fab" exact component={ snowFAB }/>
           <Route path="/works/Anti-AR Glass" exact component={ AntiAR }/>
           <Route path="/works/AI-aided Fabrication" exact component={ aiFAB }/>
           <Route path="/works/Construction of Automatic UAV" exact component={ Drone }/>
           <Route path="/works/Morphology x Mobility" exact component={ MxM }/>
           <Route path="/works/Emerging" exact component={ Luanan }/>
           <Route path="/works/A Collection of Carbonfiber Furniture" exact component={ FCollection }/>
           <Route path="/works/Topology Optimized Concrete Components" exact component={ Topology }/>
           <Route path="/works/Rockery Approximation" exact component={ Rockery }/>
           {/* <Route path="/works/:title" exact component={ WorkPageDetail } /> */}
         </section>
      </div>
      </Scrollbars>
    );
  }
}

export default withRouter(App);
