const Workboard = styled.div`
    display:${props=>props.init===false?"flex":"none"};
    align-items: center;
    // background: red;
    transform-origin: 0px 0px;
    animation: ${props => {
        if (props.open === false&&props.init===false) {
            //console.log("init",props.init)
            return (
                keyframes`
                  0%{transform:scaleY(0.0001);opacity: 0; height:1px;display:flex;} 
                  5%{transform:scaleY(0.0001);opacity: 0; height:auto;display:flex;}
                  60%{transform:scaleY(0.6);opacity: 0;height: auto;display:flex;}
                  100%{transform:scaleY(1);opacity: 1;height: auto;display:flex;}
                  
          `)
        } else if (props.open === true&&props.init===false) {
            //console.log("ss")
            return (
                keyframes`
                    0%{transform:scaleY(1);opacity: 1;height: auto;display:flex;}
                    60%{transform:scaleY(0.6);opacity: 0;height: auto;display:flex;}
                    100%{transform:scaleY(0.0001);opacity: 0;height:1px;display:flex;}
          `)
        }
    }} 0.2s forwards ease;
`
