export default function MyMap () {
  const [data, setData] = useState([])

  // function getAuthors(){
  //   console.log('effect')
  //   fetch('/posts/post/', {
  //     method: 'GET',
  //     headers: {
  //         'Accept': 'application/json',
  //         'Content-Type': 'application/json'
  //     },
  //     }).then(resp => {
  //       setData(resp.json())
  //     })}

  React.useEffect(() => {
    // 使用浏览器 API 更新文档标题
    mapboxgl.accessToken = 'pk.eyJ1Ijoieml4dW4xOTk3IiwiYSI6ImNsNjNlcHB3ZjBrNzMzY2x2enB0eWRibnEifQ.MdyLJHMi7XvbQP02U6f9LQ';
    // getAuthors();
    const data = '/media/formatted_posts.json';
    // console.log('data', data)
    const cluster = true;
    const fontSize = 32;
    const DEFAULT_COLOR = [29, 145, 192];

    function excludeNull(coordinates){
      if(coordinates[0]===null){return [-122.15, 37.52];}
      else if(coordinates[1]===null){return [-122.15, 37.52];}
      else {return coordinates;}
    }

    function formatAuthor(author){
      if(author==='hide'){ return '🦉';}
      else if(author===null){ return '👻';}
      else if(author==='哼是我'){return '🌊';}
      else if(author==='tester'){return '👾';}
      else if(author==='Eeeee'){return '🐧';}
      else if(author==='烦死了'){return '🌹';}
      else if(author==='泣血蔷薇'){return '🐒';}
      else if(author==='浅试一下'){return '👸';}
      else if(author==='聪明勉'){return '🐣';}
      else if(author==='泣血蔷薇'){return '🐒';}
      else if(author==='rr'){return '🐡';}
      else if(author==='河豚'){return '🍦';}
      else if(author==='输入法'){return '🌻';}
      else if(author==='甩头金葵'){return '🦊';}
      else if(author==='强奸犯老夫子'){return '😈';}
      else if(author==='纵火犯'){return '🔥';}
      else if(author==='童年'){return '🐖';}
      else if(author==='带刺玫瑰'){return '🐏';}
      else if(author==='梅干菜'){return '🍚';}
      else if(author==='远届'){return '👀';}
      else if(author==='大界'){return '🤖';}
      else {return '🌲';}
    }

    const map = new mapboxgl.Map({
      container: 'map',
      style: 'mapbox://styles/mapbox/dark-v10',
      // renderWorldCopies: false,
      style: 'mapbox://styles/zixun1997/cl69hnmfj000z15o4vwc8qhpe',
      center: [120, 30],
      zoom: 6,
      minZoom:2,
      maxZoom:9,
      pitch: 65,
    });

    const myScatterplotLayer = new MapboxLayer({
        id: 'deckgl-circle',
        type: ScatterplotLayer,
        data: [
          {position: [-122.15, 37.52], color: [0, 0, 255], radius: 1000}
        ],
        getPosition: d => d.position,
        getFillColor: d => d.color,
        getRadius: d => d.radius,
        opacity: 1
      })


    const textLayer = cluster
      ? new MapboxLayer({
          id: 'twitter-topics-tagmap',
          type:TagmapLayer,
          data,
          characterSet: 'auto',
          getLabel: x => formatAuthor(x.label),
          getPosition: x => excludeNull(x.coordinates),
          minFontSize: 14,
          maxFontSize: fontSize * 2 - 14
        })
      : new MapboxLayer({
          id: 'twitter-topics-raw',
          data,
          characterSet: 'auto',
          getText: d => formatAuthor(d.label),
          getPosition: x => excludeNull(x.coordinates),
          getColor: d => DEFAULT_COLOR,
          getSize: d => 20,
          sizeScale: fontSize / 20
        });


    

    map.on('load', () => {
        const firstLabelLayerId = map.getStyle().layers.find(layer => layer.type === 'symbol').id;
        map.addLayer(myScatterplotLayer,firstLabelLayerId);
        map.addLayer(textLayer,firstLabelLayerId)
      });
  });

  return (

    <div id='map' style={{width:'150%',height:'150%'}}>
    </div>

  )
}
