
import React, { Component ,useState, createRef} from 'react';
import styles from './work.module.scss';
import { Link } from 'react-router-dom';

export default ({ dataSource }) =>{
    const [tmp, setTmp] = useState(null)
    // console.log(dataSource)
    return <div className={ styles.worklist } style={{whiteSpace:'pre-line'}}>
        <ul 
        // style={{
        //         padding: tmp===null?'':'5px 0px',
        //     }}
            >
            {
                dataSource && dataSource.map(item=>{
                    // console.log(item.cover)
                    return(
                    <li key={item.id}
                    onMouseEnter={()=>{setTmp(item.title)}}
                    onMouseLeave={()=>{setTmp(null)}}
                    // style={{
                    //     padding: tmp===item.title?'20px 0px':'',
                    // }}
                    >
                        <Link to={ `/works/${item.title}` }
                        style={{
                            // background: tmp===item.title?'linear-gradient(to right, rgba(255,255,255,0.5), rgba(255,255,255,0),rgba(255,255,255,0))':'',
                            background: tmp===item.title?'rgba(255,255,255,0.25)':'',
                            // filter: tmp===item.title?'':'blur(5px)',
                            padding: '20px min(2vw,20px)',
                            borderRadius: 'min(2vw,20px)',
                        }}>
                             <div className={styles.workbrief}>
                                 <h3
                                 style={{
                                    fontWeight: tmp===item.title?'300':'',
                                    color: tmp===item.title?'rgba(0,255,0,0.8)':'',
                                    }}
                                 >{item.title}</h3>
                                 <p>{item.brief}</p>
                                 <div className={styles.tag}>
                                     { item.tags && item.tags.map(i=><span key={i}>{i}</span>) }
                                 </div>
                             </div>

                             {/* <div className={ styles.cover }>
                                 <img src={require('../../assets/images/workList_cover_280x280/'+item.cover+'.jpg')} style={{opacity:'75%'}}/>
                             </div>                       */}
                        </Link>
                     </li>
                    )
                }
                    
                )
            }
        </ul>
    </div>
}
