// import styles from '../HomePage/home.module.scss';


const Wrapbox=styled.div`
    position: absolute;
    height: 100%;
    width: 96%;
    left: 2%;
    padding: 0px 0px;
`
const Mutibox=styled.div`
    display: flex;
    position:relative;
    width:100%;
    height:100%;
    flex-direction:row;
    align-items: space-between;
    color:rgb(250,250,250);
    padding: 0px 0px;
`
const Biobox=styled.div`
    width: 100%;
    font-weight: 200;
    font-size:14px;
    padding: 0px 0px;
`
const Rightbox=styled.div`
    text-align: right;
    font-weight: 400;
    margin-bottom: 2px;
    background-color:rgba(0,255,0,0.5);
    cursor: pointer;
    padding: 0px 0px;
`
const Leftbox=styled.div`
    text-align: left;
    font-weight: 500;
    margin-top: 14px;
`
const LRinline=styled.div`
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    margin-top: 2px;
`
const Subleft=styled.div`
    width: 75%;
    position: relative;
    left: 2%;
    font-weight: 300;
`
const Timebox=styled.div`
    text-align: right;
    font-weight: 300;
    font-style: italic;
`

const Workboard = styled.div`
    display:${props=>props.init===false?"flex":"none"};
    align-items: center;
    // background: red;
    transform-origin: 0px 0px;
    animation: ${props => {
        if (props.open === false&&props.init===false) {
            //console.log("init",props.init)
            return (
                keyframes`
                  0%{transform:scaleY(0.0001);opacity: 0; height:1px;display:flex;} 
                  5%{transform:scaleY(0.0001);opacity: 0; height:auto;display:flex;}
                  60%{transform:scaleY(0.6);opacity: 0;height: auto;display:flex;}
                  100%{transform:scaleY(1);opacity: 1;height: auto;display:flex;}
                  
          `)
        } else if (props.open === true&&props.init===false) {
            //console.log("ss")
            return (
                keyframes`
                    0%{transform:scaleY(1);opacity: 1;height: auto;display:flex;}
                    60%{transform:scaleY(0.6);opacity: 0;height: auto;display:flex;}
                    100%{transform:scaleY(0.0001);opacity: 0;height:1px;display:flex;}
          `)
        }
    }} 0.5s forwards ease;
`

